import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { BehaviorSubject, Observable } from "rxjs";
import { map } from "rxjs/operators";

import { environment } from "environments/environment";
import { User, Role } from "app/auth/models";
import { ToastrService } from "ngx-toastr";
import { CryptoService } from "./cryptograph.service";

@Injectable({ providedIn: "root" })
export class AuthenticationService {
  //public
  public currentUser: Observable<User>;

  //private
  private currentUserSubject: BehaviorSubject<User>;

  /**
   *
   * @param {HttpClient} _http
   * @param {ToastrService} _toastrService
   */
  constructor(
    private _http: HttpClient,
    private _toastrService: ToastrService,
    private aes: CryptoService
  ) {
    this.currentUserSubject = new BehaviorSubject<User>(
      JSON.parse(this.aes.getItem("currentUser"))
    );
    this.currentUser = this.currentUserSubject.asObservable();
  }

  // getter: currentUserValue
  public get currentUserValue(): User {
    return this.currentUserSubject.value;
  }

  /**
   *  Confirms if user is admin
   */
  get isAdmin() {
    return (
      this.currentUser && this.currentUserSubject.value.role === Role.Admin
    );
  }

  /**
   *  Confirms if user is client
   */
  get isClient() {
    return (
      this.currentUser && this.currentUserSubject.value.role === Role.Client
    );
  }

  registeration(userDetail: object): Observable<any> {
    return this._http
      .post<any>(`${environment.apiUrl}/users/new-user`, userDetail)
      .pipe(
        map((res) => {
          return res;
        })
      );
  }

  login(userDetail: object) {
    return this._http
      .post<any>(`${environment.apiUrl}/users/login-user`, userDetail)
      .pipe(
        map((user) => {
          // login successful if there's a jwt token in the response
          if (user && user.token) {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            this.aes.setItem("currentUser", JSON.stringify(user.userDetail));
            this.aes.setItem("token", JSON.stringify(user.token));

            // notify
            this.currentUserSubject.next(user);
          }

          return user;
        })
      );
  }

  /**
   * User logout
   *
   */
  logout() {
    // remove user from local storage to log user out
    localStorage.clear();
    // notify
    this.currentUserSubject.next(null);
  }
}
