import { NgModule } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { RouterModule, Routes } from "@angular/router";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { HTTP_INTERCEPTORS, HttpClientModule } from "@angular/common/http";

import "hammerjs";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { TranslateModule } from "@ngx-translate/core";
import { ToastrModule } from "ngx-toastr"; // For auth after login toast

import { CoreModule } from "@core/core.module";
import { CoreCommonModule } from "@core/common.module";
import { CoreSidebarModule, CoreThemeCustomizerModule } from "@core/components";

import { coreConfig } from "app/app-config";

import { AppComponent } from "app/app.component";
import { LayoutModule } from "app/layout/layout.module";
import { AuthGuard } from "./auth/helpers/auth.guards";
import { JwtInterceptor } from "./auth/helpers/jwt.interceptor";
import { SharedModule } from "./shared/shared.module";

const appRoutes: Routes = [
  {
    path: "auth",
    loadChildren: () =>
      import("./modules/auth/auth.module").then((m) => m.AuthModule),
  },
  {
    path: "design",
    loadChildren: () =>
      import("./modules/design/design.module").then((m) => m.DesignModule),
    canActivate: [AuthGuard],
  },
  {
    path: "item-master",
    loadChildren: () =>
      import("./modules/item-master/item-master.module").then((m) => m.ItemMasterModule),
    canActivate: [AuthGuard],
  },
  {
    path: "karigar",
    loadChildren: () =>
      import("./modules/karigar/karigar.module").then((m) => m.KarigarModule),
    canActivate: [AuthGuard],
  },
  {
    path: "ledger",
    loadChildren: () =>
      import("./modules/customer-ledger/customer-ledger.module").then((m) => m.CustomerLedgerModule),
    canActivate: [AuthGuard],
  },
  {
    path: "customer",
    loadChildren: () =>
      import("./modules/user/user.module").then((m) => m.UserModule),
    canActivate: [AuthGuard],
  },
  {
    path: "order",
    loadChildren: () =>
      import("./modules/order/order.module").then(
        (m) => m.OrderModule
      ),
    canActivate: [AuthGuard],
  },
  {
    path: "grn",
    loadChildren: () =>
      import("./modules/grn/grn.module").then(
        (m) => m.GrnModule
      ),
    canActivate: [AuthGuard],
  },
  {
    path: "whole-sale",
    loadChildren: () =>
      import("./modules/wholesale/wholesale.module").then(
        (m) => m.WholesaleModule
      ),
    canActivate: [AuthGuard],
  },
  {
    path: "",
    redirectTo: "/customer",
    pathMatch: "full",
  },
  {
    path: "**",
    redirectTo: "/auth/error", //Error 404 - Page not found
  },
];

@NgModule({
  declarations: [AppComponent],
  imports: [
    SharedModule.forRoot(),
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes, {
      scrollPositionRestoration: "enabled", // Add options right here
      relativeLinkResolution: "legacy",
    }),
    TranslateModule.forRoot(),

    //NgBootstrap
    NgbModule,
    ToastrModule.forRoot(),
    // Core modules
    CoreModule.forRoot(coreConfig),
    CoreCommonModule,
    CoreSidebarModule,
    CoreThemeCustomizerModule,

    // App modules
    LayoutModule,
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
