import { Injectable } from "@angular/core";
import * as CryptoJS from "crypto-js";

@Injectable({
  providedIn: "root",
})
export class CryptoService {
  constructor() {}
  encrypt(data) {
    return CryptoJS.AES.encrypt(
      JSON.stringify(data),
      "secret123456145674125896",
      { iv: "secret1234561456" }
    ).toString();
  }
  decrypt(data) {
    if (data !== null || data !== "undefined") {
      var bytes = CryptoJS.AES.decrypt(
        data.toString(),
        "secret123456145674125896",
        { iv: "secret1234561456" }
      );
      return JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
    }
    return data;
  }

  setItem(key, value, json = true) {
    var ciphertext;
    if (json === true)
      ciphertext = CryptoJS.AES.encrypt(
        JSON.stringify(value),
        "secret123456145674125896",
        { iv: "secret1234561456" }
      );
    else {
      if (typeof value === "number") value = value.toString();
      ciphertext = CryptoJS.AES.encrypt(value, "secret123456145674125896", {
        iv: "secret1234561456",
      });
    }
    localStorage.setItem(key, ciphertext);
  }

  getItem(key, json = true) {
    if (localStorage.getItem(key) !== null) {
      var bytes = CryptoJS.AES.decrypt(
        localStorage.getItem(key).toString(),
        "secret123456145674125896",
        { iv: "secret1234561456" }
      );

      if (json === true) {
        var data = bytes.toString(CryptoJS.enc.Utf8);
        return JSON.parse(data);
      } else return bytes.toString(CryptoJS.enc.Utf8);
    }
    return localStorage.getItem(key);
  }

  removeItem(key, json = true) {
    return localStorage.removeItem(key);
  }
}
