import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { Observable } from "rxjs";
import { map, tap } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class UserService {
  private baseUrl = environment.apiUrl
  private path = 'customers'
  constructor(private _http: HttpClient) {}

  create(addData): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/create`, addData)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  getAll(): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/get-all`, {})
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  getDetailById(UserId): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/get-by-id`, UserId)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  delete(UserId): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/delete`, UserId)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  update(updateData): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/update`, updateData)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }
}
