import { ModuleWithProviders, NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { SweetalertComponent } from "./sweetalert/sweetalert.component";
import { SweetAlert2Module } from "@sweetalert2/ngx-sweetalert2";
import { ExportPdfComponent } from './export-pdf/export-pdf.component';
import { ModalAddCustomerComponent } from './modal-add-customer/modal-add-customer.component';
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { ReactiveFormsModule } from "@angular/forms";
import { ImageModalComponent } from "./image-modal/image-modal.component";

@NgModule({
  declarations: [ModalAddCustomerComponent,SweetalertComponent, ExportPdfComponent, ModalAddCustomerComponent,ImageModalComponent],
  imports: [CommonModule, SweetAlert2Module.forRoot(),NgbModule,ReactiveFormsModule],
  exports: [SweetalertComponent],
})
export class SharedModule {
  static forRoot(): ModuleWithProviders<SharedModule> {
    return {
      ngModule: SharedModule,
    };
  }
}
