import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { time } from "console";
import Swal from "sweetalert2";

@Component({
  selector: "app-sweetalert",
  templateUrl: "./sweetalert.component.html",
  styleUrls: ["./sweetalert.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class SweetalertComponent implements OnInit {
  id: any;
  index: any;
  constructor() {}

  ngOnInit(): void {}
  fire(icon, title, message) {
    Swal.fire({
      icon: icon,
      title: title,
      text: message,
    });
  }

  /// timer
  fireWithTimer(icon, title, message, timer) {
    Swal.fire({
      icon: icon,
      title: title,
      text: message,
      timer: timer,
    });
  }

  async delete(fn: () => void) {
    await Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#e46226",
      cancelButtonColor: "#E42728",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        fn();
        this.fire("success", "deleted", "Sucessfully deleted");
      }
    });
  }
  async customDelete(fn: () => void, heading, text) {
    await Swal.fire({
      title: "Are you sure?",
      text: text,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#e46226",
      cancelButtonColor: "#E42728",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {

        fn();
        this.fire("success", `${heading}`, "Sucessfully deleted");
      }
    });
  }

  sidetoast(icon, title) {
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 5000,
      timerProgressBar: true,
      onOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    Toast.fire({
      animation: true,
      icon: icon,
      title: title,
    });
  }
}
