import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { SweetalertComponent } from "../sweetalert/sweetalert.component";
import { UserService } from "app/modules/user/service/user.service";

@Component({
  selector: "app-modal-add-customer",
  templateUrl: "./modal-add-customer.component.html",
  styleUrls: ["./modal-add-customer.component.scss"],
})
export class ModalAddCustomerComponent implements OnInit {
  customerFrom: FormGroup = this.fb.group({
    full_name: ["", [Validators.required]],
    email: [""],
    phone: ["", [Validators.required]],
    alternateNumber: [""],
    country: [null, [Validators.required]],
    city: [null, [Validators.required]],
    customerType: [null, [Validators.required]],
    address: [""],
  });
  public submitted = false;
  country = ["Pakistan", "India", "Canada", "UK", "USA"];
  city = ["Karachi", "Islamabad", "Lahore", "Multan"];
  customerType = [
    "Stitching",
    "Wholesale Fabric Customer",
    "Wholesale Fabric Supplier",
  ];

  constructor(
    public activeModal: NgbActiveModal,
    private fb: FormBuilder,
    private _serviceData: UserService,
    private _sw: SweetalertComponent
  ) {}

  onSubmit(fromValue) {
    this.submitted = true;
    if (this.customerFrom.invalid) {
      return;
    }

    this._serviceData.create(fromValue).subscribe(
      (res) => {
        this._sw.fire("success", "Customer", `Record Successfully Add..`);
        this.customerFrom.reset();
        this.close();
      },
      (err) => {
        this._sw.fire("error", "Customer", err.error.message);
      }
    );
  }

  ngOnInit(): void {}
  get f() {
    return this.customerFrom.controls;
  }

  close() {
    this.activeModal.close();
  }
}
